@import '../../../styles/mixins';
@import '../../../styles/variables';
@import '../../../styles/utilities';

.slide_item {
    position: relative;
    width: 100%;
    height: 600px;
    background-position: top center;
    background-repeat: no-repeat;
    background-size: cover;

    @include m-breakpoint-up(lmedium) {
        height: 750px;
    }

    @include m-breakpoint-up(xlarge) {
        height: 600px;
    }

    @include m-breakpoint-up(slarge) {
        height: 825px;
    }

    .slide_wrapper {
        position: absolute;
        top: -3px;
        left: 0;
        width: 100%;
        height: 103%;
        background: linear-gradient(180deg, #071E3D 0%, rgba(7, 30, 61, 0.4) 40%);

        display: flex;
        justify-content: flex-start;
        align-items: center;
        max-width: unset !important;

        color: $clr-wah;

        .slide_content {
            max-width: calUni(1440px);

            @include m-breakpoint-up(slarge){
                margin: auto;
                min-width: calUni(1440px)
            }

        }
        
        h1 {
            text-shadow: 1px 1px 2px $clr-dark;
        }

        .overview {
            width: 100%;
            font-size: $fs-18;
            text-shadow: 1px 1px 2px $clr-dark;

            
            @include m-breakpoint-up(lmedium) {
                width: 70%;
            }
            @include m-breakpoint-up(xlarge) {
                width: 45%;
            }
        }

        .button {
            background-color: $clr-danger !important;
            text-transform: capitalize !important;
        }
    }
}